import request from '@/utils/request'
// 获取订单结算信息
export const getCheckout = (obj) => {
  console.log(obj)
  //   const { mode, cartIds, goodsId, goodsNum, goodsSkuId } = obj
  return request.get('/checkout/order', {
    params: {
      delivery: 10, // 10 快递配送 20 门店自提
      couponId: 0, // 优惠券ID 传0 不使用优惠券
      isUsePoints: 0, // 积分 传0 不使用积分
      ...obj
    }
  })
}

// 提交订单
export const submitOrder = (obj) => {
  console.log('hhh', obj)

  return request.post('/checkout/submit', {
    delivery: 10, // 10 快递配送
    couponId: 0,
    isUsePoints: 0,
    payType: 10, // 余额支付
    ...obj
  })
}

// 获取订单列表
export const getOrderList = (dataType, page) => {
  console.log(dataType, page)
  return request.get('/order/list', {
    params: {
      dataType,
      page
    }
  })
}

// 取消订单
export const cancelOrder = (orderId) => {
  return request.post('/order/cancel', {
    orderId
  })
}

// 确认收货
export const ackReceipt = (orderId) => {
  return request.post('/order/receipt', {
    orderId
  })
}
