import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login' // === '@/views/login/index.vue'
import Layout from '@/views/layout'
import Search from '@/views/search'
import SearchList from '@/views/search/list.vue'
import Pay from '@/views/pay'
import MyOrder from '@/views/myorder'
import ProDetail from '@/views/prodetail'
import Home from '@/views/layout/home.vue'
import Category from '@/views/layout/category.vue'
import Cart from '@/views/layout/cart.vue'
import User from '@/views/layout/user.vue'
import store from '@/store'
import EditAddress from '@/views/layout/editAddress.vue'
import SelAddress from '@/views/layout/selAddress.vue'
Vue.use(VueRouter)
const router = new VueRouter({
  routes: [
    { path: '/login', component: Login },
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    },
    { path: '/search', component: Search },
    { path: '/searchlist', component: SearchList },
    // 动态路由传参 确认商品 路由携带参数 商品id
    { path: '/prodetail/:id', component: ProDetail },
    { path: '/pay', component: Pay },
    { path: '/myorder', component: MyOrder },
    { path: '/editaddress', component: EditAddress },
    { path: '/seladdress', component: SelAddress }
  ]
})

// 所有的路由在真正倍访问到之间，都会先经过全局前置守卫
// 只有全局前置守卫放行了，才能到达目标页面
// 全局前置导航守卫
// 1. to   到那里去，到哪去的完整路由信息对象 （路径 参数）
// 2. from 从那里来，从哪去的完整路由信息对象 （路径 参数）
// 3. next 是否放行
//    （1） 如果next()调用，就是放行
//    （2） next(路径)拦截到某个路由页面

// 定义数组 专门用户存放所哟u需要权限访问的页面
const authUrls = ['/pay', '/myorder']

router.beforeEach((to, from, next) => {
  // 判断同.path 是否在authUrls里面
  if (!authUrls.includes(to.path)) {
    // 非权限页面直接放行
    next()
    return
  }
  // 权限页面判断

  const token = store.getters.token
  // 判断 token 是否存在
  if (token) {
    next() // 存在  放行
  } else {
    next('/login')// 不存在 拦截值 /login
  }
})
export default router
