<template>
  <div class="pay">
    <van-nav-bar left-arrow title="确认订单" @click-left="goback" />

    <!-- 地址选择 -->
    <div class="address">
      <div class="icon-left">
        <van-icon name="location" size="16" />
      </div>
      <div class="info" v-if="defaultAddressId">
        <div class="address-info">{{ defaultAddress.region.province+defaultAddress.region.city+defaultAddress.region.province+ defaultAddress.detail}}</div>
        <div class="recipient-info">
          <div class="name">{{ defaultAddress.name }}</div>
          <div class="tel">{{defaultAddress.phone}}</div>
        </div>
      </div>

      <div class="info" v-else @click="$router.push('/editaddress')">
      <!-- <div class="info" v-else @click="selAddress"> -->
        请先选择配送地址
      </div>

      <div class="icon-right" @click="selAddress">
        <van-icon name="arrow" size="16" />
      </div>

    </div>

    <!-- 订单明细 - -->
    <div class="order-detail">
      <div class="list">

        <div class="goods_item" v-for="item in order.goodsList" :key="item.goods_id">
          <div class="pro-img">
            <img :src="item.goods_image" alt="">
          </div>
          <div class="right">
            <p class="tit text-ellipsis-2">
              {{ item.goods_name}}
            </p>
            <p class="info">
              <span class="count">x{{ item.total_num }}</span>
              <span class="price">¥{{ item.goods_price_min}}</span>
            </p>
          </div>
        </div>
      </div>
      <!-- <div class="flow-num-box">
        <span>
          共 {{order.orderTotalNum}} 件商品，合计：
        </span>
        <span class="money">￥{{order.orderTotalPrice}}</span>
      </div> -->

      <!-- 支付明细 费用组成 -->
      <div class="pay-detail">
        <div class="pay-item">
          <span>订单总金额：</span>
          <span class="red">￥{{order.orderTotalPrice }}</span>
        </div>

        <div class="pay-item">
          <span>优惠券：</span>
          <span>无优惠券可用</span>
        </div>

        <div class="pay-item">
          <span>配送费用：</span>
          <!-- <span v-if="!selectedAddress">请先选择配送地址</span>
          <span v-else class="red">+￥0.00</span> -->
          <span class="red">+￥0.00</span>
        </div>
      </div>

      <!-- 支付方式 -->
      <div class="pay-mode">
        <div class="title">
          <span>支付方式</span>
        </div>
        <div class="mode-item">
          <div class="mode-icon-left">
            <van-icon name="cash-o" size="20" />
          </div>
          <span class="mode-name">余额支付</span>
          <span class="money">可用:￥{{personal.balance}}元</span>
          <div class="mode-icon-right">
            <van-icon name="arrow" size="18" />
          </div>
        </div>
      </div>
      <!-- 留言 -->
      <div class="tipmsg">
        <textarea v-model="remark" placeholder="选填：买家留言（50字内）" name="" id="" cols="30" rows="10"></textarea>
      </div>
    </div>
    <!-- 底部 -->
    <div class="footer">
      <div class="left">
        <!-- 合计 -->
        <span>共 {{order.orderTotalNum}} 件</span>
        <span>实付款:</span>
        <div class="price">
          <span>¥</span>
          <span class="price-num"> {{order.orderTotalPrice }} </span>
        </div>
      </div>
      <!-- 确认支付 -->
      <div class="btn ack" @click="submitOrder">确认支付</div>
    </div>
  </div>
</template>

<script>
import { getAddressList, getdefaultAddressId } from '@/api/address'
import { getCheckout, submitOrder } from '@/api/order'
export default {
  name: 'PayIndex',
  data () {
    return {
      order: [],
      personal: '',
      defaultAddressId: 0,
      defaultAddress: '',
      editAddress: false,
      areaList: [],
      longAddress: '',
      remark: ''
    }
  },
  computed: {

    selectedAddress () {
      // 这里地址管理非主线业务，直接获取第一个项作为选中的地址
      return this.addressList[0] || {}
    },
    mode () {
      return this.$route.query.mode
    },
    cartIds () {
      return this.$route.query.cartIds
    },
    goodsId () {
      return this.$route.query.goodsId
    },
    goodsNum () {
      return this.$route.query.goodsNum
    },
    goodsSkuId () {
      return this.$route.query.goodsSkuId
    }
  },
  created () {
    this.getAddressList()
    this.getCheckoutOrder()
  },
  methods: {
    goback () {
      this.$router.go(-1)
    },
    selAddress () {
      console.log(this.$route.path)
      // this.$router.push(`/selAddress?backUrl=${this.$route.path}`)
      this.$router.push({
        path: '/selAddress',
        query: {
          backUrl: this.$route.path
        }
      })
    },
    handleEvent () {

    },
    async getAddressList () {
      const { data: { list } } = await getAddressList()
      console.log(list)
      const selAddressId = this.$route.query.selAddressId
      if (selAddressId) {
        this.defaultAddressId = selAddressId
        this.defaultAddress = list.filter(item => item.address_id === selAddressId)[0]
      } else {
        const { data: { defaultId } } = await getdefaultAddressId()
        this.defaultAddressId = defaultId
        this.defaultAddress = list.filter(item => item.address_id === defaultId)[0]
      }
    },
    async getCheckoutOrder () {
      const { data: { order, personal } } = await getCheckout({
        mode: this.mode,
        cartIds: this.cartIds,
        goodsId: this.goodsId,
        goodsNum: this.goodsNum,
        goodsSkuId: this.goodsSkuId
      })
      this.order = order
      this.personal = personal
      // if (this.mode === 'cart') {
      //   const { data: { order, personal } } = await getCheckout({
      //     mode: this.mode,
      //     cartIds: this.cartIds
      //   })
      //   this.order = order
      //   this.personal = personal
      // }
      // if (this.mode === 'buyNow') {
      //   const { data: { order, personal } } = await getCheckout({
      //     mode: this.mode,
      //     goodsId: this.goodsId,
      //     goodsNum: this.goodsNum,
      //     goodsSkuId: this.goodsSkuId
      //   })
      //   this.order = order
      //   this.personal = personal
      // }
    },
    async submitOrder () {
      console.log({
        mode: this.mode,
        cartIds: this.cartIds,
        goodsId: this.goodsId,
        goodsNum: this.goodsNum,
        goodsSkuId: this.goodsSkuId,
        remark: this.remark
      })

      const res = await submitOrder({
        mode: this.mode,
        cartIds: this.cartIds,
        goodsId: this.goodsId,
        goodsNum: this.goodsNum,
        goodsSkuId: this.goodsSkuId,
        remark: this.remark
      })
      console.log(res)
      this.$toast.success('支付成功')
      this.$router.replace('/myorder')
    }
  }
}
</script>

<style lang="less" scoped>
.pay {
  min-height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
  gap: 5px;
}

.address {
  background-color: #fff;
  position: relative;
  margin: 0 10px;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 5px;

  .address-info {
    height: 20px;
    overflow: hidden;
    line-height: 20px;
    font-size: 16px;
  }

  .recipient-info {
    font-size: 10px;
    display: flex;
    gap: 5px;
  }

  .icon-right {
    position: absolute;
    right: 10px;
  }
}

.order-detail {
  background-color: #fff;
  border-radius: 10px;
  margin: 0 10px;
  padding: 10px;

  .list {
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}

.goods_item {
  height: 80px;
  background-color: #fff;
  display: flex;

  .pro-img {
    img {
      width: 80px;
      display: block;
    }
  }

  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;

    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;

      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: end;
  padding: 0 10px;
  font-size: 14px;

  .money {
    color: #fa2209;
  }
}

.pay-detail {
  border-top: 1px solid #aaaaaa;

  .pay-item {
    margin: 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }

  .red {
    color: #ff3c3c;
  }
}

.pay-mode {
  padding: 10px;
  border-top: 1px solid #aaaaaa;

  .title {
    line-height: 30px;
  }

  .mode-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 5px;

    .mode-icon-right {
      position: absolute;
      right: 0px;
    }
  }
}

.tipmsg {
  border-top: 1px solid rgba(221, 219, 219, 0.644);
  display: block;

  textarea {
    display: block;
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #efefef;
  font-size: 14px;

  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  // background-color: #f5f5f5;
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 10px;

  .left {
    display: flex;
    align-items: center;
    gap: 5px;

    .price {
      color: #ff3c3c;

      .price-num {
        font-size: 16px;
      }
    }
  }

  .btn {
    border-radius: 15px;
    text-align: center;
    width: 100px;
    height: 30px;
    line-height: 30px;
    background-color: #ff3c3c;
    color: #fff;
  }
}
</style>
