/**
 * 约定通用键名
 */
// 用户购物信息
const INFO_KEY = 'shopping_info'
// 搜索历史
const HISTORY_KEY = 'history_key'
// 地址信息
const ADDRESS_KEY = 'address_key'

// 获取本地用户信息
export const getInfo = () => {
  const defaultObj = { token: '', userId: '' }
  const result = localStorage.getItem(INFO_KEY)
  return result ? JSON.parse(result) : defaultObj
}
// 用户信息本地持久化
export const setInfo = (obj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}
// 移除本地用户信息
export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取本地搜索历史
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_KEY)
  return result ? JSON.parse(result) : []
}
// 搜索历史本地持久化存储
export const setHistoryList = (arr) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(arr))
}

/**
 * 用户地址列表持久化
 */
export const getAddressListStorage = () => {
  const result = localStorage.getItem(ADDRESS_KEY)
  return result ? JSON.parse(result) : []
}
export const setAddressListStorage = (arr) => {
  localStorage.setItem(ADDRESS_KEY, JSON.stringify(arr))
}
// 删除地址xinxi
export const removeAddress = () => {
  localStorage.removeItem(ADDRESS_KEY)
}
// 删除地址项
export const removeAddressItem = (removeId) => {
  const list = getAddressListStorage()
  const newList = list.filter(item => item.id !== removeId)
  setAddressListStorage(newList)
}
// 获取某地址项
export const getAddressItem = (addressId) => {
  const list = getAddressListStorage()
  const res = list.find(item => item.id === addressId)
  return res
}
// 编辑地址项
export const editAddressItem = (item) => {
  let list = getAddressListStorage()
  console.log(item, 'eeeee')
  list = list.map(element => {
    return element.id === item.id ? item : element
  })
  setAddressListStorage(list)
}
// 新增地址项
export const addAddressItem = (item) => {
  const list = getAddressListStorage()
  list.push(item)
  console.log(list)
  setAddressListStorage(list)
}
