<template>
    <div class="prodetail">

      <van-nav-bar
      title="商品详情"
      left-arrow
      @click-left="$router.go(-1)"
      />

      <!-- <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white"> -->
      <van-swipe  :autoplay="2000" @change="onChange">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image.external_url" alt="">
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{current+1}}/{{ images.length }}</div>
        </template>
      </van-swipe>

      <!-- 商品信息 -->
       <div class="info">
        <!-- 价格 已售 -->
        <div class="title">
          <div class="price">
            <span class="nowprice">￥{{ proDetail.goods_price_min }}</span>
            <span class="oldprice">￥{{ proDetail.goods_price_max }}</span>
          </div>
          <div class="sellcount">已售{{ proDetail.goods_sales }}件</div>
        </div>

        <!-- 商品简单描写 -->
        <div class="msg">
          {{ proDetail.goods_name }}
        </div>

        <!-- 商品服务 -->
        <!-- 左右部件构成一个服务信息 -->
        <!-- 服务项-->
        <!-- <div class="service" v-for="item in serviceList" :key="item.service_id"> -->
        <div class="service" v-for="item in serviceList" :key="item.service_id">
          <div class="left-words">
            <span><van-icon name="passed" />{{item.name}}</span>
          </div>
          <!-- 服务详情信息 -->
          <div class="right-icon">
            <!-- 点击图标 弹出(公共弹窗)服务详细描述 -->
            <van-icon name="arrow" />
          </div>
        </div>
       </div>

      <!-- 商品评价 -->
      <div class="comment">
        <!-- 评论标题 -->
        <div class="comment-title">
          <!-- 评论(数目) -->
          <div class="left">评论({{ commentNum }})</div>
          <!-- 右箭头 -->
          <div class="right">
            查看更多<van-icon name="arrow"></van-icon>
          </div>
        </div>

        <!-- 评论展示 -->
          <div class="comment-list">
          <!-- 一条评论 -->
          <div class="comment-item" v-for="item in commentList" :key="item.comment_id">
            <div class="top">
              <!-- 头像 -->
                <img :src="item.user.avatar_url|| defaultImg" alt="">
                <!-- 用户名 -->
                <div class="name">
                  {{item.user.nick_name}}
                </div>
                <!-- 评分 -->
                <van-rate
                  :value="item.score/2"
                  :size="25"
                  color="#ffd21e"
                  void-icon="star"
                  void-color="#eee"
                />
            </div>
            <div class="content">
              {{item.content}}
            </div>
            <div class="time">{{item.create_time}}</div>
          </div>
          </div>
      </div>

      <!-- 商品详情 -->
      <!-- <div><img :src="proDetail.goods_image" alt=""></div> -->
      <!-- <div ref="img"></div> -->
      <div class="desc" v-html="proDetail.content"></div>
      <!-- 底部 -->
      <div class="footer">
        <div @click="$router.push('/')" class="icon-home">
          <van-icon name="wap-home-o" />
          <span>首页</span>
        </div>
        <div @click="$router.push('/cart')" class="icon-cart">
          <span v-if="cartTotal > 0" class="num">{{ cartTotal }}</span>
          <van-icon name="shopping-cart-o" />
          <span>购物车</span>
        </div>
        <div class="btn">
          <div @click="onChangeMode('cart')"  class="btn-add">加入购物车</div>
          <div @click="onChangeMode('buyNow')" class="btn-buy">立刻购买</div>
        </div>
      </div>

      <!-- 加入购物车/立即购买 ————公共弹窗 -->
       <!-- v-if 动态渲染 -->
       <van-action-sheet v-model="showPannel" :title="mode ==='cart'? '加入购物车':'立即购买'">
        <div class="product">
          <!-- 商品标题 -->
           <div class="product-title">
            <!-- 商品图片 -->
            <div class="left">
              <img :src="proDetail.goods_image" alt="">
            </div>
            <div class="right">
                <!-- 价格 -->
                <div class="price">
                  <span>￥</span>
                  <span class="nowprice">{{ proDetail.goods_price_min  }}</span>
                </div>
                <!-- 数量选择 -->
                <div class="sel">
                  <CountBox v-model="selectNum"></CountBox>
                  <!-- 库存 -->
                  <span>库存{{ proDetail.stock_total }}</span>
                </div>
            </div>
           </div>

          <!-- 确认按钮 -->
          <div class="ack-btn">
            <div @click="addCart" class="btn add" v-if="mode === 'cart'">加入购物车</div>
            <div @click="buyNow"  class="btn buy" v-else>立即购买</div>
          </div>
        </div>
      </van-action-sheet>
    </div>
</template>

<script>

import { getProDetail, getServiceList, getCommentList } from '@/api/product'
import defaultImg from '@/assets/default-avatar.png'
import { addCart } from '@/api/cart'
import CountBox from '@/components/CountBox.vue'
import loginConfirm from '@/mixins/loginConfirm.js'
export default {
  name: 'ProDetail',
  mixins: [loginConfirm],
  components: {
    CountBox
  },
  data () {
    return {
      proDetail: '',
      images: [],
      current: 0,
      serviceList: [],
      commentList: [],
      commentNum: 0,
      showPannel: false,
      mode: 'cart',
      defaultImg,
      selectNum: 1,
      cartTotal: 0
    }
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    }

  },
  async created () {
    this.getProDetail()
    this.getServiceList()
    this.getCommentList()
    this.cartTotal = this.$store.getters['cart/cartTotal']
  },
  methods: {
    async getProDetail () {
      const { data: { detail } } = await getProDetail(this.goodsId)
      this.proDetail = detail
      this.images = detail.goods_images
      console.log(detail)
    },
    async getServiceList () {
      const { data: { list } } = await getServiceList(this.goodsId)
      this.serviceList = list
    },
    async getCommentList () {
      const { data } = await getCommentList({
        goodsId: this.goodsId,
        limit: 2
      })
      this.commentList = data.list
      this.commentNum = data.total
    },
    onChange (index) {
      this.current = index
    },
    async addCart () {
      if (this.loginConfirm()) {
        console.log('proDetail--未登录')
        return
      }
      // 已登录 // 加入购物车
      // 将数据传入后端
      console.log('tainjia ')

      const { data } = await addCart({
        // 商品ID
        goodsId: this.goodsId,
        //  商品数量
        goodsNum: this.selectNum,
        //  商品SKUID
        goodsSkuId: this.proDetail.skuList[0].goods_sku_id
      })
      this.cartTotal = data.cartTotal
      console.log(data)
      this.$toast('加入购物车成功')
      this.showPannel = false
    },
    buyNow () {
      if (this.loginConfirm()) {
        return null // 未登录 直接返回
      }
      // 跳转到支付界面
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.goodsId,
          goodsSkuId: this.proDetail.skuList[0].goods_sku_id,
          goodsNum: this.selectNum
        }
      })
    },
    onChangeMode (val) {
      this.mode = val
      this.showPannel = true
    }
  }
}
</script>

<style lang = "less" scoped>
.prodetail {
  :deep .van-icon-arrow-left {
    color: #ee0f0f;
  }
  img{
    width: 100%;
    display: block;
  }
  .custom-indicator {
    position: absolute;
    right: 5px;
    bottom: 5px;
    padding: 2px 5px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
  }
  .info{
    padding: 10px;
  }
  .title{
    display: flex;
    justify-content: space-between;
    .nowprice{
      color: rgb(220, 68, 68);
      font-size: 15px;
    }
    .oldprice{
      color: gray;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }
    .sellcount{
      color: gray;
      font-size: 16px;
    }
  }
  .msg{
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }
  .service{
    display: flex;
    justify-content: space-between;
    line-height: 25px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;;
    .left-words{
      span{
        margin-left: 10px;
      }
      .van-icon{
        margin-right: 5px;
        color: #ee0f0f;
      }
    }
  }
  .comment{
    padding: 10px;
    background-color: #fafafa;
    .comment-title{
      display: flex;
      justify-content: space-between;
      .right{
        color: antiquewhite;
      }
    }
    .comment-item{
      font-size: 16px;
      line-height: 30px;
      .top{
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 20px;
        img{
          width: 20px;
          height: 20px;
        }
        .name{
          margin: 0 10px;
          text-align: center;
        }
      }
      .time{
        color: #646464;
      }
    }

  }
  .desc{
    width: 100%;
    overflow: scroll;
    ::v-deep img{
      display: block;
      width: 100%!important;
    }
  }
  .footer{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 45px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home,
    .icon-cart{
      display: flex;
      flex-direction: column; //纵向排列
      align-items: center;
      justify-content: center;
      position: relative;
      .van-icon{
        font-size: 18px;
      }
      .num{
        position: absolute;
        top: -1px;
        right: 10px;
        background-color: red;
        color: white;
        padding: 1px 1px;
        font-size: 9px;
        border-radius: 50%;
        z-index: 99;
      }

    }
    .btn{
      display: flex;
    }
    .btn-buy,
    .btn-add{
      background-color: #ffc400;
      width: 120px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
      color: #fff;
    }
    .btn-add{
      border-radius: 20px 0 0 20px ;
    }
    .btn-buy {
      border-radius:  0 20px 20px 0;
      background-color: #fe5630;
    }
  }
}
.product{
  .product-title{
    display: flex;
    justify-content: flex-start;
    .left{
      margin: 10px;
      img{
        width: 100px;
        height: 100px;
      }
    }
    .right{
       padding: 10px;
      display: flex;
      flex-direction:column;
      justify-content: space-between;
      .price{
        color: #fe5630;
      }
      .nowprice{
        font-size: 30px;
      }
      .sel{
        display: flex;
        align-items: center;
      }
    }
  }

  .btn{
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .buy {
    background-color: #fe5630;
  }
}

</style>
