import { getCartList, updateCart, delSelect } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  getters: {
    cartTotal (state) {
      console.log('getters-cartTotal')
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选中数目
    selNum (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    selTotalPrice (state, getters) {
      return getters.selCartList.reduce((sum, item) => {
        return sum + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2)
    },
    isCheckedAll (state) {
      return state.cartList.every(item => item.isChecked)
    }

  },
  mutations: {
    setCartList (state, newList) {
      state.cartList = newList
    },
    changeCount (state, obj) {
      const { goodsId, goodsNum } = obj
      const goods = state.cartList.find(item => item.goodsId === goodsId)
      goods.goods_num = goodsNum
    },
    toggleCheckAll (state, status) {
      state.cartList.forEach(element => {
        element.isChecked = status
      })
    }
  },
  actions: {
    async getCartListAction (context) {
      const { data } = await getCartList()
      data.list.forEach(element => {
        element.isChecked = true
      })
      context.commit('setCartList', data.list)
    },
    async updateCartAciton (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      // 先修改本地数据
      context.commit('changeCount', { goodsId, goodsNum })
      // 同步到后台
      await updateCart({ goodsNum, goodsId, goodsSkuId })
    },
    // 删除选择购物车数据
    async delSelect (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(item => item.id)
      await delSelect(cartIds)
      Toast('删除成功')
      // 重新拉取最新的购物车数据（重新渲染）
      context.dispatch('getCartListAction')
    },
    // 删除某个商品
    async delOne (context, goodsId) {
      await delSelect([goodsId])
      Toast('删除成功')
      // 重新拉取最新的购物车数据（重新渲染）
      context.dispatch('getCartListAction')
    }

  }
}
