<template>
    <div class="myorder">
      <van-nav-bar
      title="我的订单"
      left-arrow
      @click-left = '$router.go(-1)'
      />

      <van-tabs v-model="activeName">
        <van-tab title="全部" name="all">
          <!-- <template #title>
            <van-icon name="more-o" />选项
          </template> -->
                  </van-tab>
        <van-tab title="待支付" name="payment"></van-tab>
        <van-tab title="待发货" name="delivery"></van-tab>
        <van-tab title="待收货" name="received"></van-tab>
        <van-tab title="待评价" name="comment"></van-tab>
        <van-tab title="退款/售后" name="aftersale"></van-tab>
      </van-tabs>
      <OrderListItem v-for="item in orderList.data" :key= "item.id" :listItem="item"></OrderListItem>

    </div>
</template>

<script>
import { getOrderList } from '@/api/order'
import OrderListItem from '@/components/OrderListItem.vue'
export default {
  name: 'MyorderIndex',
  components: {
    OrderListItem
  },
  data () {
    return {
      activeName: this.$route.query.dataType || 'all',
      page: 1,
      orderList: []
    }
  },
  watch: {
    activeName: {
      immediate: true,
      handler () {
        this.getOrderList()
      }
    }
  },
  methods: {
    handleEvent () {

    },
    async getOrderList () {
      const { data: { list } } = await getOrderList(this.activeName, this.page)
      this.orderList = list
    }
  }
}
</script>

<style lang = "less" scoped>
.myorder{
  background-color: #f5f5f5;
  min-height: 100vh;
}
</style>
