<template>
  <div class="searchlist">
    <van-nav-bar title="商品列表" left-arrow @click-left="$router.go(-1)" />
    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value ="querySearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class='tool' name="apps-o" size="30" />
      </template>
    </van-search>

    <!-- 排序类型选择 -->
    <div class="sort-btns">
      <div class="sort-item" @click="handleSort('all')">综合</div>
      <div class="sort-item" @click="handleSort('sales')">销量</div>
      <div class="sort-item" @click="handleSort('price')">价格</div>
    </div>

    <!-- 搜索商品列表 -->
    <!-- <div class="goods-list" v-if="nothing">什么都没有</div> -->
    <div class="goods-list">
      <GoodsItem v-for="item in productlist" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import { getProductList } from '@/api/product'
import GoodsItem from '@/components/GoodsItem.vue'
export default {
  name: 'SearchList',
  components: {
    GoodsItem
  },
  data () {
    return {
      productlist: [],
      page: 1,
      nothingState: false,
      sortPrice: 0
    }
  },
  computed: {
    querySearch () {
      return this.$route.query.search // 获取动态菜蔬 商品种类ID
    },
    queryCategoryId () {
      return this.$route.query.categoryId
    },
    nothing () {
      return this.productlist.length === 0
    }
  },
  async created () {
    const { data: { list } } = await getProductList({
      categoryId: this.$route.query.categoryId,
      goodsName: this.querySearch,
      page: this.page
    })
    console.log('this.$route.query.categoryId', this.$route.query.categoryId)
    this.productlist = list.data
  },
  methods: {
    async handleSort (val) {
      if (val === 'price') { this.sortPrice = (this.sortPrice + 1) % 2 }
      console.log(this.sortPrice.toString())
      const { data: { list } } = await getProductList({
        sortType: val, // all-按综合搜索(默认)，sales-按销量搜索，price-按价格搜索
        sortPrice: this.sortPrice.toString(),
        categoryId: this.queryCategoryId,
        goodsName: this.querySearch,
        page: this.page
      })
      this.productlist = list.data
    }
  }
}
</script>

<style lang="less" scoped>
.searchlist {
  ::v-deep .van-search__action {
    // background-color: #b84032;
    // color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }

  ::v-deep .van-icon-arrow-left {
    color: #6d50bd;
  }

  // 商品样式
  .goods-list {
    background-color: #f6f6f6;
  }

  .sort-btns {
    display: flex;
    justify-content: center;
    /* Center the child elements horizontally */
    align-items: center;

    /* Center the child elements vertically if needed */
    .sort-item {
      width: 30%;
      margin: 0 10px;
      /* Optional: Add some space between the items */
      text-align: center;
      font-size: 20px;
    }
  }

}
</style>
