<template>
  <div class="edit-address">
    <van-nav-bar title="地址编辑" left-arrow @click-left=$router.go(-1) />
    <van-address-edit
      :area-list="areaList"
      :address-info="addressInfo"
      show-posta
      show-delete
      show-set-default
      show-search-result
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
      @delete = 'ondelete'
      />
  </div>
</template>

<script>
import { getRegionTree, addAddress, editAddress, removeAddress } from '@/api/address'
// import areaList from '@/utils/getAreaList'
import loginConfirm from '@/mixins/loginConfirm.js'
import { addAddressItem, editAddressItem, getAddressItem, removeAddressItem } from '@/utils/storage'
export default {
  name: 'EditAddress',
  mixins: [loginConfirm],
  data () {
    return {
      addressId: NaN,
      areaCodeIDList: {},
      areaList: {},
      addressInfo: {},
      provinceNum: 9,
      cityNum: 0,
      countyNum: 0
    }
  },
  computed: {
  },
  async created () {
    const { data: { list } } = await getRegionTree()
    // 通过vue.set()向响应式对象添加响应式属性
    // this.processAreaData(list)
    // 直接修改响应式对象
    this.setAreaList(list)

    // 获取初始值
    this.addressId = +this.$route.query.addressId
    if (this.addressId) {
      this.addressInfo = getAddressItem(+this.$route.query.addressId)
    }
  },
  mounted () {

  },
  methods: {
    ondelete (e) {
      // 发送删除请求
      removeAddress(e.id)
      // 删除本地
      removeAddressItem(e.id)

      this.$router.go('-1')
    },
    onSave (e) {
      console.log(e.value)
      console.log(typeof (e))
      console.log(e)

      console.log('s?')

      if (!this.loginConfirm()) {
        console.log('ddl')
      }
      const { addressDetail, areaCode, city, county, isDefault, name, postalCode, province, tel } = e
      console.log(isDefault, postalCode)
      const form = {
        name,
        phone: tel,
        region: [
          {
            label: province,
            value: this.areaCodeIDList.province_list[areaCode.slice(0, 2) + '0000']
          },
          {
            label: city,
            value: this.areaCodeIDList.city_list[areaCode.slice(0, 4) + '00']
          },
          {
            label: county,
            value: this.areaCodeIDList.county_list[areaCode]
          }
        ],
        detail: addressDetail
      }
      e.address = e.province + e.city + e.county + e.addressDetail
      if (this.addressId) {
        // 编辑
        // 发送编辑请求
        editAddress(this.addressId, form)
        // 修改本地
        editAddressItem(e)
        // 回退router
        this.$router.go(-1)
      } else {
        console.log('新增')
        // 新增
        addAddress(form)
        // 删除本地
        addAddressItem(e)
        // 回退router
        this.$router.go(-1)
      }
    },
    getCode (level) {
      if (level === 1) {
        this.provinceNum += 1
        this.cityNum = 0
        this.countyNum = 0
      } else if (level === 2) {
        this.cityNum += 1
        this.countyNum = 0
      } else if (level === 3) {
        this.countyNum += 1
      }
      const code = this.provinceNum.toString().padStart(2, '0') +
        this.cityNum.toString().padStart(2, '0') +
        this.countyNum.toString().padStart(2, '0')
      return code
    },
    setAreaList (data) {
      const areaList = {
        province_list: {},
        city_list: {},
        county_list: {}
      }
      const areaCodeIDList = {
        province_list: {},
        city_list: {},
        county_list: {}
      }
      for (const key in data) {
        const province = data[key]
        // 省级数据提取
        const provinceCode = this.getCode(province.level)
        areaList.province_list[provinceCode] = province.name
        areaCodeIDList.province_list[provinceCode] = province.id
        for (const cityKey in province.city) {
          const city = province.city[cityKey]
          // 市级数据提取
          const cityCode = this.getCode(city.level)
          areaList.city_list[cityCode] = city.name
          areaCodeIDList.city_list[cityCode] = city.id
          for (const regionKey in city.region) {
            const county = city.region[regionKey]
            // 区/县级数据提取
            const countyCode = this.getCode(county.level)
            areaList.county_list[countyCode] = county.name
            areaCodeIDList.county_list[countyCode] = county.id
          }
        }
      }
      this.areaList = areaList
      this.areaCodeIDList = areaCodeIDList
    },
    processAreaData (obj) {
      for (const key in obj) {
        const item = obj[key]
        if (item.level === 1) {
          this.provinceNum += 1
          this.cityNum = 0
          this.countNum = 0
          const code = this.provinceNum.toString().padStart(2, '0') + this.cityNum.toString().padStart(2, '0') + this.countNum.toString().padStart(2, '0')
          this.$set(this.areaList.province_list, [code], item.name)
        } else if (item.level === 2) {
          this.cityNum += 1
          this.countNum = 0
          const code = this.provinceNum.toString().padStart(2, '0') + this.cityNum.toString().padStart(2, '0') + this.countNum.toString().padStart(2, '0')
          // Object.assign(this.areaList.province_list, { [code]: item.name })
          this.$set(this.areaList.city_list, [code], item.name)
          // Object.assign(this.areaList.city_list, { [code]: item.name })
        } else if (item.level === 3) {
          this.countNum += 1
          const code = this.provinceNum.toString().padStart(2, '0') + this.cityNum.toString().padStart(2, '0') + this.countNum.toString().padStart(2, '0')
          this.$set(this.areaList.county_list, [code], item.name)
          // Object.assign(this.areaList.county_list, { [code]: item.name })
        }
        // 递归处理 city 和 region
        if (item.city) {
          this.processAreaData(item.city)
        }
        if (item.region) {
          this.processAreaData(item.region)
        }
      }
    }
  }

}
</script>

<style lang="less" scoped></style>
