import { getAddressList, getdefaultAddressId, getRegionTree } from '@/api/address'
import { getAreaCode } from '@/utils/address'
import { getInfo, setInfo, setAddressListStorage, getAddressListStorage } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo(),
      addressList: getAddressListStorage()
    }
  },
  mutations: {
    // 所有mutations的第一个参数 都是state
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    },
    setAddressList (state, arr) {
      state.addressList = arr
      setAddressListStorage(arr)
    }
  },
  getters: {

  },
  actions: {
    async getAddressList (context) {
      const { data } = await getAddressList()
      const { data: { defaultId } } = await getdefaultAddressId()
      const { data: { list } } = await getRegionTree()
      const addresslist = data.list.map(item => {
        return {
          id: item.address_id,
          name: item.name,
          tel: item.phone,
          address: item.region.province + item.region.city + item.region.region + item.detail,
          isDefault: defaultId === item.address_id,
          areaCode: getAreaCode({
            privinceId: item.province_id, cityId: item.city_id, regionId: item.region_id
          }, list),
          province: item.region.province,
          city: item.region.city,
          county: item.region.region,
          addressDetail: item.detail
        }
      })
      context.commit('setAddressList', addresslist)
      console.log('adddd,', addresslist)
    },
    logout (context) {
      // 个人信息要重置
      context.commit('setUserInfo', {})

      // 购物车信息要重置 (跨模块调用 mutation)  cart/setCartList
      context.commit('cart/setCartList', [], { root: true })
    }
  }
}
