<template>
    <div class="order_list_item">
        <div class="title">
            <div class="time">{{listItem.pay_time}}</div>
            <div class="status">{{ listItem.state_text }}</div>
        </div>

        <div class="list">
            <div class="item" v-for="item in listItem.goods" :key = item.goods_id>
                <div class="goods_img">
                    <img :src="item.goods_image" alt="">
                </div>
                <div class="goods_content text-ellipsis-2">
                    {{item.goods_name}}
                </div>
                <div class="goods_trade">
                    <p>￥ {{item.goods_price}}</p>
                    <p>x {{item.total_num}} 件</p>
                </div>
            </div>
        </div>

        <div class="total">
            总共 {{ listItemTotalNum }} 件 合计￥ {{ listItemTotalPrice}}
        </div>
        <div class="actions">
            <!-- 订单状态：进行中 order_status===10 -->
            <div v-if="listItem.order_status === 10">
                <span v-if="listItem.pay_status === 10">立刻付款</span>
                <span v-else-if="listItem.delivery_status === 10" @click="onCancel(listItem.order_id)">申请取消</span>
                <span v-else-if="listItem.delivery_status === 20 || listItem.delivery_status === 30">确认收货</span>
            </div>
            <!-- 订单状态：完成 order_status===30 -->
            <div v-if="listItem.order_status === 30">
                <span>评价</span>
            </div>
        </div>
    </div>
</template>

<script>
import { cancelOrder } from '@/api/order'
export default {
  props: {
    listItem: {

    }
  },
  data () {
    return {

    }
  },
  computed: {
    listItemTotalNum () {
      return this.listItem.goods.reduce((sum, item) => sum + (+item.total_num), 0)
    },
    listItemTotalPrice () {
      return this.listItem.goods.reduce((sum, item) => sum + (+item.total_pay_price), 0)
    }
  },
  methods: {
    async onCancel (id) {
      // 发送取消请求
      await cancelOrder(id)
      //   Toast('取消成功')
    //   this.$router.push('/myorder')
      // 重新进入order页
    }
  }
}
</script>

<style lang = "less" scoped>
.order_list_item{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 0.5px 2px 0 rgba(00, 0, 0, 0.05);
    // min-height: 100vh;
    margin: 10px;
    padding: 10px;
    color: #333;
    font-size: 13px;
    border-radius: 8px;
    .title{
        display: flex;
        justify-content: space-between;
        // padding: 5px;
        height: 25px;
        line-height: 25px;
        margin-bottom: 5px;
        .status{
            color: #f18a14;
        }
    }
    .list{
        .item{
            display: flex;
            align-items: center;
            .goods_img{
                padding: 0 10px;
                img{
                    border-radius: 5px;
                    width: 60px;
                }
            }
            .goods_content{
                flex: 2;
                line-height: 18px;
                max-height: 36px;
            }
            .goods_trade{
                color: #686767;
                font-size: 12px;
                line-height: 18px;
                text-align: right;
            }
        }
    }
}
.total{
    text-align: right;
}
.actions{
    text-align: right;
    span{
        display: inline-block;
        height: 28px;
        line-height: 28px;
        color: #414141;
        border: 0.5px solid #727272;
        font-size: 14px;
        padding: 0 15px;
        border-radius: 5px;
        margin: 10px,0;
    }
}
</style>
