// 登录相关请求接口
import request from '@/utils/request'

// 获取搜索列表
export const getProductList = (obj) => {
  const { categoryId, goodsName, page } = obj
  // const { categoryId, goodsName, page, sortPrice, sortType } = obj
  console.log(categoryId, goodsName, page)
  return request.get('/goods/list', {
    params: {
      // sortType,
      // sortPrice,
      categoryId,
      goodsName,
      page
    }
  })
}

// 获取商品详情
export const getProDetail = (goodsId) => {
  return request.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}

// 获取商品保障服务
export const getServiceList = (goodsId) => {
  return request.get('/goods.service/list', {
    params: {
      goodsId
    }
  })
}

// 获取商品评价
export const getCommentList = (obj) => {
  const { goodsId, limit } = obj
  return request.get('/comment/listRows', {
    params: {
      goodsId, // 目标商品ID
      limit // 获取评论数
    }
  })
}

// 获取商品评价数
export const getCommentNum = (goodsId) => {
  return request.get('/comment/total', {
    params: {
      goodsId // 目标商品ID
    }
  })
}
