
export const getAreaCode = (addressIdObj, provinceCityCountyList) => {
  /**
     * 通过 地址列表的地址 获取对应的key
     * 将其转换为 Van-address-edit需要的地址代码 ：
     *  areaList 为对象结构，包含 province_list、city_list、county_list 三个 key。
     *   每项以地区码作为 key，省市区名字作为 value。
     *   地区码为 6 位数字，前两位代表省份，中间两位代表城市，后两位代表区县，以 0 补足 6 位。比如北京的地区码为 11，以 0 补足 6 位，为 110000。
     *
     * addressIdObj:省市区ID
     * province_city_county_list: 省市区列表
     */
  // 为保证 地区编号长度，省份编码从10开始，即北京——10
  //   const values = Object.keys(province_city_county_list)
  const { privinceId, cityId, regionId } = addressIdObj
  const provinceCode = Object.keys(provinceCityCountyList)
    .indexOf(privinceId.toString()) + 10
  const cityCode = Object.keys(provinceCityCountyList[privinceId].city)
    .indexOf(cityId.toString()) + 1
  const countyCode = Object.keys(provinceCityCountyList[privinceId].city[cityId].region)
    .indexOf(regionId.toString()) + 1
  const areaCode = provinceCode.toString() + cityCode.toString().padStart(2, '0') + countyCode.toString().padStart(2, '0')
  return areaCode
}

