import store from '@/store'
import axios from 'axios'
import { Toast } from 'vant'
// 创建 axios 实例，将来对创建出来的实例进行自定义配置
// 好处：不会污染原始的axios实例
const instance = axios.create({
  baseURL: 'https://smart-shop.itheima.net/index.php?s=/api',
  // baseURL: 'https://apifoxmock.com/m1/4944280-4601912-default',
  timeout: 1000
  // headers: {
  //   'Access-Token': this.$store.getters.token,
  //   platform: ['H5']
  // }
})

// --自定义配置 --请求/响应 拦截器
// 添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么

  // 开启loading 禁止背景单机 （节流处理）
  // 自定义加载图标
  Toast.loading({
    message: '加载中...',
    forbidClick: true, // 禁用背景点击
    loadingType: 'spinner', // 配置loading图标
    duration: 0 // 自动消失事件
  })

  const token = store.getters.token
  if (token) {
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }

  return config
}, function (error) {
  // 对请求错误做些什么
  console.log('requset-error')
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么
  const res = response.data
  if (res.status !== 200) {
    // 给提示  Toast式单例模式，后面的Toast调用了，会将前一个Toast覆盖
    // 同时只能存在一个
    Toast(res.message)
    // 抛出一个错误promise
    return Promise.reject(res.message)
  } else {
    // 清楚loading效果
    Toast.clear()
  }

  return res // (默认axios会多包装一层data，需要响应拦截器处理一下)
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  // 服务器返回了响应代码，但它在 2xx 范围之外
  // 根据状态码进行不同的处理
  if (error.response) {
    console.error('Server error:', error.response.status)
    switch (error.response.status) {
      case 401:
      // 例如：处理未授权错误
        alert('未授权，请登录！')
        break
      case 404:
      // 处理找不到资源的错误
        alert('资源未找到！')
        break
      case 500:
      // 处理服务器错误
        alert('服务器错误，请稍后再试！')
        break
      default:
        alert('请求失败，请稍后再试！')
    }
  } else if (error.request) {
    // 请求已发出，但未收到响应
    console.error('Network error:', error.message)
    alert('网络错误，请检查您的网络连接！')
  } else {
  // 其他错误
    console.error('Error:', error.message)
    alert('发生未知错误，请稍后再试！')
  }

  return Promise.reject(error)
})

export default instance
