<template>
    <div class="edit-address">
      <van-nav-bar title="选择地址" left-arrow @click-left=$router.go(-1) />
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        :disabled-list="disabledList"
        disabled-text="以下地址超出配送范围"
        default-tag-text="默认"
        @add="onAdd"
        @edit="onEdit"
        @select="onSelect"
        />
    </div>
  </template>

<script>
import loginConfirm from '@/mixins/loginConfirm.js'
import { getAddressListStorage } from '@/utils/storage'
export default {
  name: 'EditAddress',
  mixins: [loginConfirm],
  data () {
    return {
      chosenAddressId: '',
      disabledList: [],
      list: getAddressListStorage()
      /**
       *list:[
       {
       * id:地址id
       * name：收件人
       * tel：联系电话
       * address 详细地址
       * }
       ]
       */
    }
  },
  computed: {
  },
  created () {
  },
  mounted () {

  },
  methods: {
    onSelect (e) {
      console.log('xuze ')
      // 在回退前，将参数添加到当前 URL 的 query 中
      this.$router.push({
        path: this.$route.query.backUrl,
        query: {
          selAddressId: e.id
        }
      })
    },
    onAdd () {
      console.log('ADDD')
      this.$router.push('/editaddress')
    },
    onEdit (e) {
      console.log(e)
      console.log(e.id)
      // this.$router.push({
      //   path: '/editaddress',
      //   query: {
      //     addressId: e.id
      //   }
      // })
      this.$router.push(`/editaddress?addressId=${e.id}`)
      // this.$router.push(`/editaddress?addressID=${e.id}`)
    }
  }

}
</script>

<style lang="less" scoped></style>
