<template>
  <div class="cart">
    <!-- 标题 -->
    <div class="head">
      <div class="title">
        <span>购物车</span>
        <!-- 商品总数 -->
        <span class="num">({{ cartTotal }})</span>
      </div>
      <!-- 管理btn-----负责切换商品数据选择器 以及 底部结算栏状态 -->
      <!-- <button @click="onManage">管理</button> -->
      <button @click="manage = !manage">{{ manage ? "完成" : "管理" }}</button>
    </div>

    <!-- 购物车列表 -->
    <div class="proList">
      <!-- 商品选项 -->
      <div class="selProduct" v-for="item in cartList" :key="item.goods_id">
        <!-- 复选框 -->
        <van-checkbox v-model="item.isChecked"></van-checkbox>
        <!-- 商品简单描述 -->
        <!-- 图片 -->
        <div class="proimg" @click="$router.push(`/prodetail/${item.goods.goods_id}`)">
          <img :src="item.goods.goods_image" alt="">
        </div>
        <div class="right">
          <!-- 商品描述 -->
          <div class="dec" @click="$router.push(`/prodetail/${item.goods.goods_id}`)">
            <span>{{ item.goods.goods_name }}</span>
          </div>
          <div class="price-num">
            <!-- 商品价格 -->
            <div class="price">
              <span>￥</span>
              <span class="nowprice">{{ item.goods.goods_price_min }}</span>
            </div>
            <div class="mana-del-num">
              <!-- 删除商品 -->
              <van-icon v-if="manage" size="20px" color="red" name="delete-o" @click="delOne(item.id)" />
              <!-- 商品数量选择 -->
              <CountBox v-else v-model="item.goods_num"></CountBox>
            </div>
          </div>
        </div>
      </div>

    </div>

    <!-- 底部  去结算 -->
    <div class="footer">
      <div class="left">
        <!-- 全选按钮 -->
        <div class="all-check" @click="toggleCheckAll">
          <van-checkbox :value="isCheckedAll" icon-sise="18"></van-checkbox>
        </div>
        <span>全选</span>
        <!-- 合计 -->
        <div class="totol-box" v-if="!manage">
          <span>合计:</span>
          <div class="price">
            <span>¥</span>
            <span class="price-num">{{ selTotalPrice }}</span>
          </div>
        </div>
        <!-- <div class="total-price">
        </div> -->
      </div>
      <!-- 删除按钮 -->
      <div v-if="manage" class="btn del" @click="delSelPro">删除</div>
      <!-- 结算按钮 -->
      <div v-else class="btn Settlement"  @click="settlement">去结算{{ selNum }}</div>
    </div>
  </div>

</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'cartIndex',
  components: {
    CountBox
  },
  data () {
    return {
      manage: false
    }
  },
  created () {
    // 已登录用户才能直接获取购物车
    if (this.isLogin) {
      this.$store.dispatch('cart/getCartListAction')
    }
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['cartTotal', 'selTotalPrice', 'isCheckedAll', 'selNum', 'selCartList']),
    // 是否登录判断
    isLogin () {
      return this.$store.getters.token
    }
  },
  methods: {
    onClickEditAddress () {

    },
    onManage () {
      this.manage = !this.manage
    },
    toggleCheckAll () {
      this.$store.commit('cart/toggleCheckAll', !this.isCheckedAll)
    },
    delSelPro () {
      this.$store.dispatch('cart/delSelect')
    },
    delOne (goodsId) {
      console.log(goodsId)
      this.$store.dispatch('cart/delOne', goodsId)
    },
    settlement () {
      if (this.selNum === 0) {
        this.$toast('您还没有选择商品哦')
        return null
      }
      console.log('结算')
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'cart',
          cartIds: this.selCartList.map(item => item.id).join(',') // 'cartId,cartId,cartId'
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.cart {
  background-color: #f5f5f5;
  min-height: 100vh;

  .head {
    display: flex;
    justify-content: space-between;
    padding: 10px;

    .title {
      // margin-left: 10px;
      // margin-: 10px;
      display: flex;
      align-items: center;

      span {
        font-size: 30px;
      }

      .num {
        font-size: 20px;
        margin-left: 5px;
        color: rgba(54, 54, 54, 0.795)
      }
    }

    button {
      margin-right: 10px;
      border: none;
      text-decoration: underline;
      align-self: end;
    }
  }

  .proList {
    margin: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    .selProduct {
      // margin: 10px;
      padding: 10px;
      background-color: #fff;
      display: flex;
      border-radius: 10px;
      gap: 10px;

      // justify-content: start;
      .proimg {
        // margin: 0 5px;
        border-radius: 20px;
        overflow: hidden;
      }

      .proimg img {
        width: 90;
        height: 90px;
      }

      .right {
        width: 200px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .dec {
          width: 100%;
          height: 32px;
          overflow: hidden;
          font-size: 12px;
        }
        .price-num {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          .price {
            color: #f75a12;
            .nowprice {
              font-size: 24px;
            }
          }
          .van-icon {
            size: 40px;
          }
        }
      }
    }
  }

  .footer {
    // bottom: 0;
    position: fixed;
    height: 50px;
    bottom: 50px;
    width: 100%;
    background-color: #fff;
    display: flex;
    padding: 10px 20px;
    justify-content: space-between;
    align-items: center;

    .left {
      display: flex;
      gap: 5px;
      align-items: baseline;
      .totol-box{
        display: flex;
        gap: 5px;
        align-items: center;
        .price {
          color: #f75a12;
          font-size: 16px;

          .price-num {
            font-size: 22px;
          }
        }
      }
    }

    .btn {
      text-decoration: underline;
      border-radius: 15px;
      text-align: center;
    }

    .Settlement {
      width: 100px;
      height: 30px;
      line-height: 30px;
      background-color: #ff3c3c;
      color: #fff;
    }

    .del {
      border: 1px solid rgb(150, 150, 150);
      width: 70px;
    }
  }
}
</style>
